<script>
import { onMount,onDestroy } from 'svelte';
import Slide from './slide.svelte';
export let Data;
let selected = 0;
let buffer = Data;
let interval=null;
let transed = false;

buffer[selected].active = true;

function add(){
   buffer[selected].active = false;
   selected = (selected + 1)%buffer.length;
   buffer[selected].active = true;
   transed = true;
}

function sub(){
    buffer[selected].active = false;
    selected = selected - 1;
    if(selected<0)
        selected = buffer.length - 1;
    buffer[selected].active = true;
    transed = true;
}

// function automatic(){
//     if(!transed)
//         add();
//     transed = false;
// }

// onMount(function(){
//     interval = setInterval(automatic,10000);
// })
// onDestroy(function(){
//     clearInterval(interval);
