</script>
<style lang="text/postcss">.left,.right{border-radius:9999px;--text-opacity:1;color:#fff;color:rgba(255,255,255,var(--text-opacity));font-size:1.5rem;font-weight:900;z-index:10}.left:focus,.right:focus{outline:0}.left:hover,.right:hover{--border-opacity:1;border-color:#90cdf4;border-color:rgba(144,205,244,var(--border-opacity));--text-opacity:1;color:#000;color:rgba(0,0,0,var(--text-opacity))}
/*# sourceMappingURL=src/components/utils/slider/slider.svelte.map */</style>

<div class="m-auto w-screen h-64 overflow-hidden relative">
    <div class='absolute inset-0 flex items-center justify-between'>
        <button class="left" on:click={sub}>&lt;</button>
        <button class="right" on:click={add}>&gt;</button>
    </div>
    {#each buffer as slide}
        <Slide data={slide}/>
    {/each}
</div>